*{
    margin: 2px;
    /* padding: 2px; */
}
.card{
    background-color: rgb(123, 89, 123);
    height: 50vh;
    box-shadow: 0px 0px 15px gray;
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 20vh;
    width: 70vw;
    margin: auto;
}
button{
    border: 1px solid rgb(123, 89, 123);
    /* border: none; */
height: 50px;
width: 50vmax;
box-shadow: 0px 0px 10px rgb(199, 196, 196);
font-size: large;
border-radius: 0px 5px 0px 5px;
background-color: rgb(250, 227, 250);

}
button:hover{
    transform: scale(1.1);
    background-color: rgb(221, 204, 221);
}
.card-div{
    color: white;
    font-size: larger;
    border: solid white 1px;
    height: 40vh;
    margin: auto;
    width: 90vw;
    /* display: flex;
    justify-content: center;
    align-items: center; */
    flex-wrap: wrap;
    box-sizing: border-box;
    overflow-x: scroll;


}
h1{
    margin: 20px;
}